<template>
	<view>
		<view class="count-down">
			考试时间
			<text class="time">{{ isEndTime }}</text>
		</view>
		<view class="test-content">
			<view class="content-type">
				<text>问答题</text>
				<text>第{{tIndex+1}}题</text>
			</view>
			<view class="content-title p-2" v-html="questionBank[tIndex].title"></view>
			<view class="choice-question p-2" v-if="questionBank[tIndex].type=='radio'||questionBank[tIndex].type=='checkbox'||questionBank[tIndex].type=='trueOrfalse'">
				<view class="A-question p-2" v-for="(item,index) in questionBank[tIndex].options" :key="index"><text>{{letters[index]}}</text><text v-html="item"></text></view>
			</view>
			<block v-if="questionBank[tIndex].type=='completion' || questionBank[tIndex].type=='answer'"><textarea v-for="(item,index) in gapIndex" :key="index" class="textarea p-1"  placeholder="请输入答案..." /></block>
			<button type="default" class="btn" v-if="questionBank[tIndex].type=='completion'" @click="gapIndex++">添加填空</button>
		</view>
		<view class="">
			<!-- 底部导航 -->
			<view class="footer">
				<text v-if="tIndex" class="iconfont icon-shangyibu" @click="handerPrevious"></text>
				<text v-if="!tIndex" style="color: #aeaba6;" class="iconfont icon-shangyibu"></text>
				<view class="icon-num">
					<text class="iconfont icon-leimupinleifenleileibie"></text>
					<text>
						<text style="color: #5ccc84;">{{ tIndex+1 }}</text>
						/{{questionBank.length}}
					</text>
				</view>
				<view class="icon-num">
					<text class="iconfont icon-tijiao"></text>
					<text>交卷</text>
				</view>
				<text v-if="questionBank.length-1!=tIndex" class="iconfont icon-xiayibu" @click="handerNext"></text>
				<text v-if="questionBank.length-1==tIndex" style="color: #aeaba6;" class="iconfont icon-xiayibu"></text>
			</view>
		</view>
	</view>
</template>

<script>
import testApi from '@/api/testApi.js';
export default {
	data() {
		return {
			userTestId: 0,
			questionBank:[],
			tIndex:0,
			letters:['A','B','C','D','E','F'],
			gapIndex:1
			
		};
	},
	onLoad(options) {
		console.log(options);
		this.userTestId = options.id;
		this.getTestTopic();
	},	
	onUnload() {
		clearInterval(this.timer);
	},
	methods: {
		handerPrevious(){
			if(!this.tIndex) return
			this.tIndex -- 
			this.gapIndex=1
		},
		handerNext(){
			if(this.questionBank.length-1==this.tIndex) return
			this.tIndex ++
			this.gapIndex=1
		},
		async getTestTopic() {
			try {
				let res = await testApi.getTestTopicApi({ id: this.userTestId });
				console.log(res);
				this.questionBank = res.data.data.testpaper_questions
				console.log(this.questionBank);
				if (res.statusCode == 400) {
					uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
					// setTimeout(()=>{
					// 	// this.navTo('/pages/index/test-list')
					// },500)
				}
			} catch (e) {
				//TODO handle the exception
			}
		}
	}
};
</script>

<style lang="scss">
.count-down {
	padding: 25rpx 0;
	text-align: center;
	color: red;
	border-bottom: 1rpx solid red;
	border-top: 1rpx solid red;
	.time {
		padding-left: 20rpx;
	}
}
.test-content {
	.content-type {
		padding: 20rpx;
		border-bottom: 1rpx solid #f7f7f7;
		display: flex;
		justify-content: space-between;
		> text {
			&:nth-child(2) {
				color: #666;
				font-size: 28rpx;
			}
		}
	}
	.choice-question{
		>view{
			border: 1rpx solid #f7f7f7;
			margin: 20rpx 0;
			border-radius: 10rpx;
			display: flex;
			>text{
				&:nth-child(1){
					font-size: 38rpx;
					margin: 0 20rpx;
				}
			}
		}
	}
	.textarea {
		width: 92%;
		border: 1rpx solid #ccc;
		margin: 0 20rpx;
	}
	.btn{
		width: 95%;
		margin: 20rpx auto;
		background-color: #5ccc84;
		color: #fff;
	}
}
.footer {
	padding: 10rpx 0;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	align-items: center;
	justify-content: space-around;
	.icon-num {
		display: flex;
		flex-direction: column;
		align-items: center;
		font-size: 24rpx;
	}
	.iconfont {
		font-size: 40rpx;
	}
}
</style>
